import { Injectable, ViewRef, Host } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { ToolbarConfig } from './toolbarConfig';

@Injectable()
export class ToolbarService {
  public title: string = "";
  public groups: any[];
  public current: any;
  public options: any;
  public mode: string = "default";
  public rule: string[] = [];

  constructor() {
    this.groups = [
      { 
        title: 'Услуги',
        name: 'services',
        items: [
          {route: '/services', title: "Список услуг"},
          {route: '/services/add', title: "Добавить услугу"}
        ]
      },
      { 
        title: 'Анализы',
        name: 'analyzes',
        items: [
          {route: '/analyzes', title: "Список анализов"},
          {route: '/analyzes/add', title: "Добавить анализ"}
        ]
      },
      { 
        title: 'Люди',
        name: 'persons',
        items: [
          {route: '/persons', title: "Список людей"},
          {route: '/patients', title: "Список пациентов"},
          {route: '/users', title: "Список пользователей"},
          {route: '/doctors', title: "Список врачей"}
        ]
      },
      {
        title: 'Специальности',
        items: [
          {route: '/specialties', name: 'list', title: "Список специальностей"}
        ],
        route: '/specialties'
      },
      {
        title: 'Файловый менеджер',
        items: [
          {route: '/uploads', name: 'list', title: "Список загрузок"}
        ],
        route: '/uploads'
      },
      {
        title: 'Оплата',
        items: [
          {route: '/payments/add', name: 'list', title: "Оплата"}
        ]
      }
    ];
  }

  change(options: ToolbarConfig) {
    this.title = options.title;
    this.options = options;
    this.setMode('default');
    this.rule = [];

    if(options.actionsPosition) {
      if(options.actionsPosition.extend) this.rule.push('extend');
      if(options.actionsPosition.sidenav) this.rule.push('sidenav');
      if(options.actionsPosition.top) this.rule.push('top');
    }

    if(options.actions) {
      if(options.actions.search) this.rule.push('asearch');
      if(options.actions.done) {
        this.rule.push('adone');
        if(options.actions.done.ok) this.rule.push('adoneok');
        if(options.actions.done.close) this.rule.push('adoneclose');
      }
      if(options.actions.add) this.rule.push('aadd');
    }
  }

  setMode(name) : void {
    this.mode = name;
  }

  getAction(name) : Function {
    return this.options.actions[name] ? this.options.actions[name] : () => console.log('error');
  }

}



