import { Injectable } from '@angular/core';
import { Pagination } from './pagination/pagination';

@Injectable()
export class PaginationService extends Array<Pagination> {

  constructor() {
    console.log('call constructor');
    super();
  }

  setParams(params, profile?: string) {
   if(this[profile] === undefined) {
     this[profile] = new Pagination(params);
   } else {
     this[profile].setParams(params);
   }
  }

  notShow() {

  }

  clear() {

  }

  getParams(profile?: string) : any {
    if(this[profile] !== undefined) { 
      return this[profile];
    } else {
      return false;
    }
  }

  getPager(profile?: string) {
    if(this[profile] !== undefined) {
        //console.log(this.countItems, this.totalPages);
        // calculate total pages
      this[profile].totalPages = Math.ceil(this[profile].countItems / this[profile].nPerPage);

      let startPage: number, endPage: number;
      if (this[profile].totalPages <= 10) {
          // less than 10 total pages so show all
          startPage = 1;
          endPage = this[profile].totalPages;
      } else {
          // more than 10 total pages so calculate start and end pages
          if (this[profile].currentPage <= 6) {
              startPage = 1;
              endPage = 10;
          } else if (this[profile].currentPage + 4 >= this[profile].totalPages) {
              startPage = this[profile].totalPages - 9;
              endPage = this[profile].totalPages;
          } else {
              startPage = this[profile].currentPage - 5;
              endPage = this[profile].currentPage + 4;
          }
      }

      // calculate start and end item indexes
      let startIndex = (this[profile].currentPage - 1) * this[profile].nPerPage;
      let endIndex = Math.min(startIndex + this[profile].nPerPage - 1, this[profile].countItems - 1);

      // create an array of pages to ng-repeat in the pager control
      this[profile].pages = this.range(startPage, endPage + 1);
    }
  }

  setAction(action: Function, profile?: string) {
    if(this[profile] !== undefined) {
      this[profile].action = action;
    }
  }

  private range(start, stop, step?) {
    if (stop == null) {
      stop = start || 0;
      start = 0;
    }
    step = step || 1;

    let length = Math.max(Math.ceil((stop - start) / step), 0);
    let range = Array(length);

    for (let idx = 0; idx < length; idx++, start += step) {
      range[idx] = start;
    }

    return range;
  };

}



