import { Component, OnInit } from '@angular/core';
//declare var $:any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  clientHeight = document.documentElement.clientHeight - 64 - 22;
  constructor() {}

  ngOnInit(): void {
  }

  onResize(target?) {
    let toolbar = <any>document.querySelector('app-toolbar');
    let footer = <any>document.querySelector('#app-footer');
    this.clientHeight = document.documentElement.clientHeight - toolbar.offsetHeight - footer.offsetHeight;
  }
}



