import { BrowserModule } from '@angular/platform-browser';
import { NgModule, LOCALE_ID } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterializeModule } from 'angular2-materialize/src';
import { FileUploadModule } from 'ng2-file-upload';
import { PersonsModule } from './persons/persons.module';
//import { MaterialModule } from '@angular/material';

import { AppRoutingModule } from './app.routing';

import { AppComponent } from './app.component';
import { PersonFormComponent } from './persons/person-form/person-form.component';

import { ToolsService } from './shared/tools.service';
import { ToolbarService } from './shared/toolbar.service';
import { ToastService } from './shared/toast.service';
import { HelpPanelService } from './shared/help-panel.service'; 
import { FileSelectService } from './shared/file-select.service';
import { FormDataService } from './shared/form-data.service';
import { PaginationService } from './shared/pagination.service';

import { ToolbarComponent } from './shared/toolbar/toolbar.component';

@NgModule({
  declarations: [
    AppComponent,
    ToolbarComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterializeModule,
    //MaterialModule.forRoot(),
    AppRoutingModule,
    FileUploadModule
  ],
  providers: [
    ToolsService,
    ToolbarService,
    ToastService,
    HelpPanelService,
    FileSelectService,
    FormDataService,
    PaginationService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



