import { Injectable } from '@angular/core';

@Injectable()
export class HelpPanelService {
  public anchor: any;
  public show: string = 'inactive';
  private showed: boolean = false;

  constructor() {}

  getAnchor(name) {
    return this.anchor ? this.anchor : false;
  }

  setAnchor(name, options?) {
    options = options ? options : {};
    options.icon = options.icon ? options.icon: name; 
    this.anchor = options;
  }

  toggle() {
    this.showed = !this.showed;
    if(this.showed) this.open();
    else this.close();
  }

  close() {
    this.showed = false;
    this.show = 'inactive';
  }

  open() {
    this.showed = true;
    this.show = 'active';
  }

}



